
header.header {
  display: -ms-flexbox;
  display: flex;
  padding: 15px;
  margin: auto;
}
header.header h1 {
    font-weight: 400;
    margin: auto auto auto 0;
    border: 3px solid black;
    padding: 15px 20px;
}
@media only screen and (max-width: 768px) {
header.header h1 {
        font-size: 22px;
        margin: auto;
}
}
header.header h1 a {
      color: black;
      text-decoration: none;
}
header.header ul {
    list-style: none;
    margin: auto 0 auto auto;
    font-size: 17px;
}
@media only screen and (max-width: 768px) {
header.header ul {
        display: none;
}
}
header.header ul li {
      display: inline-block;
      margin-right: 15px;
      padding: 10px 0;
      font-size: 11px;
      text-transform: uppercase;
      letter-spacing: 0.16em;
      font-weight: 400;
      line-height: 1em;
}
header.header ul li button {
        border: 0;
        background: 0;
        cursor: pointer;
}
header.header ul li a {
        text-decoration: none;
        color: black;
        transition: color 300ms cubic-bezier(0.33, 0, 0.2, 1);
}
header.header ul li a.active, header.header ul li a:hover {
          color: #9e0000;
}
header.header ul li:after {
        content: '//';
}
header.header ul li:last-child {
        margin-right: 0;
}



