
@charset "UTF-8";
.map {
  position: relative;
  width: 100vw;
}
.map.ipad {
    height: auto;
    min-height: -webkit-fill-available;
}
.map.ipad .map__grid {
      height: auto;
      min-height: -webkit-fill-available;
}
.map .title-wrap {
    padding: 0;
    grid-area: 2 / 2 / 2 / 6;
    text-align: left;
}
.map .title-wrap .title {
      font-size: 2.75vw;
      margin: 0;
      padding: 0;
      font-size: 2rem;
      line-height: 1.2;
      margin-bottom: 10px;
}
.map .title-wrap .subtitle {
      font-size: 1.25rem;
      margin: 0;
      margin-bottom: 10px;
}
.map .title-wrap .year {
      font-size: 1.25rem;
      display: block;
}
.map .title-wrap .year:before {
        content: '―';
        margin-right: 0.5rem;
}
.map .title-wrap .lang {
      pointer-events: all;
}
.map .title-wrap .lang button {
        background: none;
        padding: 0;
        margin: 0 0 0 10px;
        border: none;
        cursor: pointer;
}
.map .subtitle-wrap {
    position: relative;
    z-index: 30;
    grid-area: 2 / 3;
    margin-right: 15px;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    text-align: left;
    opacity: 0;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.map .subtitle-wrap {
        margin-right: 5px;
}
}
.map .subtitle-wrap .title {
      font-size: 2rem;
      line-height: 1.2;
      letter-spacing: 0.5px;
      font-weight: 600;
      padding: 0;
      margin: 0;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.map .subtitle-wrap .title {
          font-size: 1.3rem;
}
}
.map .subtitle-wrap .subtitle {
      font-weight: 400;
      display: block;
      font-size: 1.7rem;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.map .subtitle-wrap .subtitle {
          font-size: 1rem;
}
}
.map__orientation {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 20;
    background: white;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.map__orientation svg {
      width: 30vw;
      margin-bottom: 30px;
}
.map__badges {
    position: absolute;
    z-index: 19;
    right: 0;
    top: 10vh;
    cursor: pointer;
}
.map__badges:hover li {
      transform: translateX(0) rotate(0deg);
}
.map__badges ul {
      list-style: none;
      margin: 0;
}
.map__badges li {
      width: 77px;
      margin: 10px;
      transition: transform 250ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
      transform: translateX(56px) rotate(-90deg);
      will-change: transform;
}
.map__badges li:nth-child(1) {
        transition-delay: 0;
}
.map__badges li:nth-child(2) {
        transition-delay: 80ms;
}
.map__badges li:nth-child(3) {
        transition-delay: 180ms;
}
.map__badges img {
      width: 100%;
}
.map__loader {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 20;
    background: white;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.map__loader p {
      color: #949494;
      font-weight: 500;
      letter-spacing: 0.5px;
      text-transform: uppercase;
}
.map__content {
    position: absolute;
    top: 100vh;
    left: 0;
    width: 65vw;
    padding: 10vh 10vh 10vh 5vh;
    z-index: 10;
    height: 100vh;
    background-color: #e5e5e5;
    overflow: auto;
}
.map__map {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.map__map #map {
      height: 100vh;
}
.map__map .mapboxgl-control-container {
      display: none !important;
}
.map__grid {
    display: -ms-grid;
    display: grid;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100vw;
    -ms-grid-columns: 17vw 21vw 17vw 17vw 8vw;
        grid-template-columns: 17vw 21vw 17vw 17vw 8vw;
    grid-column-gap: 5vw;
    overflow: hidden;
    pointer-events: none;
}
.map__grid--outer {
      position: relative;
      padding: 0;
      -ms-grid-rows: 10rem auto;
          grid-template-rows: 10rem auto;
      -ms-flex-align: start;
          align-items: start;
}
.map__item {
    -ms-flex-item-align: end;
        -ms-grid-row-align: end;
        align-self: end;
    transform: translate3d(0, 30px, 0);
    cursor: pointer;
    pointer-events: all;
}
.map__item:before, .map__item:after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      top: -100vh;
      width: 1px;
      background-color: #f3f3f3;
}
.map__item:before {
      left: 0;
}
.map__item:after {
      right: 0;
}
.map__item:hover .map__item-title {
      transform: translate3d(3px, 3px, 0);
}
.map__item:hover .map__item-number {
      transform: translate3d(0px, -4px, 0);
}
.map__item:last-child:after {
      display: none;
}
.map__item-title {
      position: absolute;
      top: -2rem;
      right: -1.75rem;
      white-space: nowrap;
      margin: 0;
      z-index: 100;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
      letter-spacing: 1.1px;
      transition: transform 1.2s linear;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.map__item-title {
          font-size: 1.4rem;
}
}
.map__item-number {
      top: -2rem;
      right: 0;
      line-height: 1;
      position: absolute;
      font-size: 1.5rem;
      z-index: 100;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
      transition: transform 1.2s linear;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.map__item-number {
          font-size: 1.2rem;
}
}
.map__item--more {
      display: -ms-flexbox;
      display: flex;
      position: relative;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
      background: #333;
      height: 8vw;
      width: 100%;
      transform: none;
      cursor: default;
}
.map__item--more button,
      .map__item--more a {
        cursor: pointer;
        opacity: 1;
        color: #fff;
        font-size: 1.15rem;
        font-weight: 600;
        text-decoration: none;
}
.map__item--more button {
        opacity: 0;
        background: none;
        border: none;
        position: absolute;
}
.map__item-imgwrap {
    position: relative;
    overflow: hidden;
}
.map__item-img {
    display: block;
    width: 100%;
}



