
.fade-enter-active, .fade-leave-active {
  transition: opacity 1000ms ease-in-out 500ms;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  transition: transform 500ms ease-in-out 200ms;
}
.slide-enter-active .loader,
  .slide-leave-active .loader {
    transition: transform 300ms ease-in-out;
}
.slide-enter, .slide-leave-to {
  transform: translateY(-100%);
}
.slide-enter .loader, .slide-leave-to .loader {
    transform: translateY(-50vh);
}
.page-enter-active, .page-leave-active {
  transition: transform 750ms cubic-bezier(0.33, 0, 0.2, 1), opacity 750ms cubic-bezier(0.33, 0, 0.2, 1);
}
.page-enter, .page-leave-to {
  opacity: 0;
  transform: translateY(-25px);
}
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: transform 500ms ease-in-out 50ms;
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(100%);
}
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
a.link {
  position: relative;
  color: #9e0000;
  text-decoration: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  z-index: 1;
}
a.link:after {
    display: block;
    position: absolute;
    content: '';
    background: #9e0000;
    width: 0;
    bottom: 4px;
    height: 2px;
    transition: width 500ms ease-in-out;
}
a.link:hover:after {
    width: 100%;
}
#app {
  overflow: hidden;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
#app .page {
    will-change: transform, opacity;
}



