    font-style: normal;
}
@media only screen and (max-width: 768px) {
.travel__description {
        padding-bottom: 0;
        max-width: 80vw;
}
}
.album {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.album__photo {
    position: relative;
    margin-bottom: 7vw;
}
.album__photo:nth-child(even).landscape {
      margin-left: 15vw;
}
.album__photo:nth-child(even).landscape .album__description {
        text-align: right;
}
.album__photo:nth-child(even).portrait {
      margin-top: 15vh;
}
.album__photo:nth-child(odd).landscape .album__description {
      text-align: left;
}
.album__photo.portrait .album__description {
      text-align: left;
}
.album__photo.landscape + .portrait .album__description {
      text-align: left;
}
.album__photo.portrait + .portrait .album__description {
      text-align: right;
}
.album__photo img {
      display: block;
      width: 100%;
      will-change: opacity;
      opacity: 0;
      transition: opacity 1000ms cubic-bezier(0.33, 0, 0.2, 1);
}
.album__photo img[lazy=loaded] {
      opacity: 1;
}
.album__photo img[lazy=loaded] + .loader {
      opacity: 0;
}
.album__inner {
    position: relative;
}
.album__inner:before {
      position: absolute;
      content: '';
      top: -15px;
      left: -15px;
      bottom: -15px;
      right: -15px;
      background: white;
      z-index: -1;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.album__inner:before {
          top: -10px;
          left: -10px;
          bottom: -10px;
          right: -10px;
}
}
.album__overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 10;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 700;
    font-size: 3rem;
    letter-spacing: 1.3rem;
}
.album__description {
    height: auto !important;
    padding-top: 25px;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.album__description {
        padding-top: 15px;
}
}
.album__description p {
      display: inline;
      font-family: 'Libre Baskerville';
      padding: 5px;
      font-size: 1rem;
      line-height: 2.45rem;
      font-style: normal;
      color: #000;
      background: #fff;
      box-shadow: -15px 0 0 0 #fff, 15px 0 0 0 #fff;
      box-decoration-break: clone;
      -webkit-box-decoration-break: clone;
}
@media all and (min-width: 769px) and (max-width: 1366px) {
.album__description p {
          font-size: 0.8rem;
          line-height: 2rem;
          box-shadow: -10px 0 0 0 #fff, 10px 0 0 0 #fff;
}
}
.album__link {
    width: 100%;
}
.album__link button,
    .album__link a {
      display: inline-block;
      height: auto;
      padding: 10px 25px;
      font-size: 1rem;
      line-height: 1.45rem;
      text-transform: uppercase;
      text-decoration: none;
      font-style: normal;
      font-weight: 700;
      color: #949494;
      background: none;
      border: 2px solid #949494;
      margin: 0;
      min-width: 165px;
      letter-spacing: 1.5px;
      transition: all 300ms ease-in-out;
      cursor: pointer;
}
.album__link button:last-child,
      .album__link a:last-child {
        margin-left: 25px;
}
.album__link button:hover,
      .album__link a:hover {
        color: black;
        border-color: black;
}
.album__loader {
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: opacity 600ms cubic-bezier(0.33, 0, 0.2, 1);
}



